<template>
  <div class="day">{{date}}</div>
</template>

<script>
import CalendarWeekKid from "./CalendarWeekKid.vue";
export default {
  props: ["startdate", "dayoffset", "mykids"],
  components: { CalendarWeekKid },
  computed: {
    date: function() {
      var mydate = new Date();
      mydate.setTime(
        this.startdate.getTime() + this.dayoffset * 24 * 60 * 60 * 1000
      );
      return mydate.getDate();
    }
  }
};
</script>



