<template>
  <div class="selector" v-if="week">
    <!-- CampSelector {{kid.name}} -->
    <camp-week-program-card
      @addsession="$emit('addession', {program:$event, camp:camp, kid:kid, week:week})"
      :camp="camp"
      :week="week"
      v-for="(camp, idx) in allcamps"
      :grade="kid.grade"
      :key="idx"
    ></camp-week-program-card>
  </div>
</template>
<script>
import firebase from "firebase";
import { db } from "../main";
import FieryVue from "fiery-vue";

import CampWeekProgramCard from "./CampWeekProgramCard.vue";

export default {
  fiery: true, // required to add this.$fiery to this component
  components: { CampWeekProgramCard },

  props: ["week", "kid"],
  data() {
    return {
      allcamps: this.$fiery(db.collection("camps/"), {
        sub: {
          programs: {
            sub: {
              sessions: {
                // creates an array or map on each todo object: todo.children[]
                // once, map, etc
                // query: children => children.orderBy('updated_at')
              }
            }
          }
        }
      })
    };
  }
};
</script>

<style>
.selector {
  /* position: fixed; */
  /* top: 0px; */
  /* left: 700px; */
  /* margin-top: 90px; */
  /* height: 100%; */
}
</style>



