<template>
  <div>
    <div v-for="camp in camps" :key="camp.id">
      <router-link :to="'/campadmin/' + idOf(camp) ">{{camp.campdetails.name }}</router-link>
    </div>
    <div>
      <form @submit="addCamp(newcamp)">
        <input
          v-model="newcamp.campdetails.name"
          placeholder="New Camp Name"
          class="input"
          required
        >
        <button class="button" type="submit">Add Camp</button>
      </form>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";
import { db } from "../main";
import FieryVue from "fiery-vue";

export default {
  name: "CampAdminList",
  components: {},
  fiery: true, // required to add this.$fiery to this component
  props: [],
  data() {
    return {
      camps: this.$fiery(db.collection("camps/")),
      newcamp: {
        campdetails: {
          name: "",
          url: ""
        }
      }
    };
  },
  methods: {
    idOf(camp) {
      var parts = camp[".uid"].split("/");
      return parts[parts.length - 1];
    },
    addCamp(camp) {
      const createdAt = new Date();
      db.collection("camps").add(camp);
      newcamp = {};
    },
    logout() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.replace("login");
        });
    }
  }
};
</script>
<style scoped>
</style>


