<template>
  <div>
    <div @click="openall=true" v-if="!openall">open all</div>
    <div @click="openall=false" v-else>close all</div>
    <!-- Calendar {{currentUserId}} -->
    <div class="calendar">
      <calendar-week
        v-for="index in numweeks"
        :key="index"
        :startdate="startdates[index-1]"
        :highligheddate="highlightedweek"
        :openall="openall"
        :class="{deactivated: deactivated(index-1)}"
        :index="index"
        :mykids="mykids"
        @choose="$emit('choose', $event)"
      ></calendar-week>
    </div>
  </div>
</template>

<script>
import CalendarWeek from "./CalendarWeek.vue";
export default {
  props: ["currentUserId", "mykids", "highlightedweek"],
  components: { CalendarWeek },
  data() {
    return {
      startdate: new Date(Date.UTC(2019, 5, 2)),
      numweeks: 12,
      openall: false
    };
  },
  computed: {
    startdates: function() {
      var dates = [];
      var currentDate = this.startdate;

      for (var i = 0; i < this.numweeks; i++) {
        dates.push(new Date(currentDate));
        currentDate.setTime(currentDate.getTime() + 7 * 24 * 60 * 60 * 1000);
      }
      return dates;
    }
  },
  methods: {
    deactivated(index) {
      if (
        this.highlightedweek &&
        this.startdates[index] != this.highlightedweek
      ) {
        return true;
      }
      //   console.log("true " + this.highlightedweek);

      return false;
    }
  }
};
</script>



<style lang="scss">
$day-width: 70px;
.day {
  float: left;
  width: $day-width;
  // min-height: 86px;
  font-family: "Arial Black";
  font-weight: "bold";
  border-top: 1px solid black;
  color: black;
}
.week {
  clear: both;
  width: 7 * $day-width;
  padding-bottom: 10px;
}
.day:first-child,
.day:last-child {
  color: lightgrey;
}
html {
  padding: 20px;
  list-style-type: decimal-leading-zero;
  list-style-position: inside;
}
.kid:nth-child(2) {
  background-color: #e2e2e2;
}
.kid:nth-child(3) {
  background-color: #bbbbbb;
}
.kid:nth-child(4) {
  background-color: #e2e2e2;
}
.deactivated {
  //   opacity: 0.5;
}

.kid {
  color: black;
  background-color: #bbbbbb;
  //   background-color: lightgrey;
  // border-radius: 6px;
  width: 5 * $day-width;
  font-family: "Arial";
  padding: 3px;
  margin-bottom: 1px;
  clear: both;
  margin-left: $day-width;
  margin-bottom: 3px;
}
.calendar {
  margin-left: 100px;
  text-align: left;
  width: 490px;
}
.month {
  display: block;
  margin-left: -150px;

  text-align: right;
  height: 0px;
  width: 150px;
  font-size: 50px;
  font-family: "Arial Black";
  position: sticky;
  top: 0px;
}
</style>



