<template>
  <div
    class="kid"
    @mouseover.stop="showhover = true"
    @mouseout.stop="showhover = false"
    @click="selected=!selected; $emit('choose', {startdate: startdate, kid:mykid})"
  >
    <span style="font-family:'Arial'; font-weight:bold">{{mykid.name}}</span>
    <span v-if="selected &&showhover" class="toggle">⤴️</span>
    <span v-else-if="showhover" class="toggle">⤵️</span>
    <div v-for="(registration, idx) in registrationsThisWeek" :key="idx">
      {{registration.camp + ": " + registration.program.name}}
      <span style="position:relative">
        <span style="width: 200px" v-if="showhover" @click.stop="deregister(registration)">
          <button style="position:absolute; margin-left:35px; top:0px">cancel</button>
        </span>
        <!-- <camp-details v-if="showhover" :campname="registration.camp"></camp-details> -->
      </span>
    </div>
    <camp-selector v-if="shouldshow" :week="startdate" :kid="mykid" @addession="addsession($event)"></camp-selector>
  </div>
</template>
<script>
import CampSelector from "./CampSelector.vue";
import TransitionExpand from "./TransitionExpand.vue";
import CampDetails from "./CampDetails.vue";
import { db, getid } from "../main";

export default {
  props: ["startdate", "mykid", "child", "openall"],
  fiery: true,
  components: { CampSelector, TransitionExpand, CampDetails },
  data() {
    return {
      selected: false,
      showhover: false
    };
  },
  watch: {
    selected: function(newvalue, oldvalue) {
      // if (!newvalue) {
      //   this.openall = false;
      // }
    },
    openall: function(newvalue, oldvalue) {
      if (newvalue) this.selected = true;
      else {
        this.selected = false;
      }
    }
  },
  computed: {
    shouldshow() {
      return this.selected;
    },
    registrationsThisWeek() {
      var registrations = [];
      this.mykid.registrations.forEach(registration => {
        var registrationDate = new Date();
        if (registration.week) {
          registrationDate.setTime(registration.week.seconds * 1000);
          // console.log(this.startdate + " " + registrationDate);

          if (this.startdate.getTime() == registrationDate.getTime()) {
            registrations.push(registration);
            // console.log("pushing");
          }
        }
      });
      return registrations;
    }
  },
  methods: {
    deregister: function(registration) {
      this.$fiery.remove(registration);
    },
    addsession(event) {
      //   alert(event.program.name + " " + event.kid[".uid"]);
      var kidpath = this.mykid[".uid"].split("/");
      var kidid = kidpath[kidpath.length - 1];
      //   [];
      var registration = {
        program: event.program.programdetails,
        camp: event.camp.campdetails.name,
        week: event.week,
        campid: getid(event.camp)
      };

      db.collection("kids")
        .doc(kidid)
        .collection("registrations")
        .add(registration);
    }
  }
};
</script>
<style>
/* .expand-enter defines the starting state for entering */
/* .expand-leave defines the ending state for leaving */
.expand-enter-active,
.expand-leave-active {
  transition-property: opacity, height;
}
.expand-enter,
.expand-leave-to {
  opacity: 0;
}
.toggle {
  float: right;
}
</style>



