<template>
  <div>
    <div class="month" v-if="newmonth">{{monthname}}</div>
    <div class="week">
      <calendar-day
        :startdate="startdate"
        v-for="index in 7"
        :key="index"
        :dayoffset="index"
        :mykids="mykids"
        @choose="$emit('choose', $event)"
      ></calendar-day>
      <div>
        <calendar-week-kid
          :startdate="startdate"
          @choose="$emit('choose', $event)"
          :child="child.name"
          v-for="(child, idx) in mykids"
          :mykid="child"
          :key="idx"
          :openall="openall"
        ></calendar-week-kid>
      </div>
    </div>
  </div>
</template>
<script>
import CalendarDay from "./CalendarDay.vue";
import CalendarWeekKid from "./CalendarWeekKid.vue";
const monthNames = [
  "JAN",
  "FEB",
  "MAR",
  "APR",
  "MAY",
  "JUN",
  "JUL",
  "AUG",
  "SEP",
  "OCT",
  "NOV",
  "DEC"
];

export default {
  components: { CalendarDay, CalendarWeekKid },
  props: ["startdate", "index", "mykids", "openall"],
  computed: {
    monthname: function() {
      var tempDate = new Date(this.startdate);
      tempDate.setDate(this.startdate.getDate() + 6);
      return monthNames[tempDate.getMonth()];
    },
    newmonth: function() {
      var tempDate = new Date(this.startdate);
      tempDate.setDate(this.startdate.getDate() + 7);
      //   console.log(" my index is " + index);
      return this.index == 1 || tempDate.getDate() < 8;
    }
  },
  methods: {
    choose: function(event) {
      alert("wowo");
    }
  }
};
</script>



